<template>
  <textarea ref="mde"></textarea>
</template>

<script>
import SimpleMDE from 'simplemde'
import 'simplemde/dist/simplemde.min.css'
export default {
  name: 'd2-mde',
  props: {
    // 
    value: {
      type: String,
      required: false,
      default: ''
    },
    // 
    config: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  data () {
    return {
      // 
      mde: null,
      // 
      //  https://github.com/sparksuite/simplemde-markdown-editor#configuration
      defaultConfig: {
        autoDownloadFontAwesome: false
      }
    }
  },
  mounted () {
    // 
    this.init()
  },
  destroyed () {
    // 
    this.mde = null
  },
  methods: {
    // 
    init () {
      // 
      const config = Object.assign({}, this.defaultConfig, this.config)
      // 
      this.mde = new SimpleMDE({
        ...config,
        // 
        initialValue: this.value,
        // 
        element: this.$refs.mde
      })
      this.mde.codemirror.on('change', () => {
        this.$emit('input', this.mde.value())
      })
    }
  }
}
</script>

<vue-filename-injector>
export default function (Component) {
  Component.options.__source = "src/components/d2-mde/index.vue"
}
</vue-filename-injector>
